.pzRoot {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.pzRoot p {
  margin: 0px 4px;
  font-weight: bold;
  font-size: 1.2em;
}

.arrow {
  color: #ff5722;
  cursor: pointer;
}

.arrow:hover {
  color: #64d8cb;
  font-size: 2em;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.link {
  text-decoration: none;
  color: azure;
  font-weight: bold;
}


