.Button {
  background-color: transparent;
  border: 3px solid azure;
  border-radius: 10px;
  color: azure;
  outline: none;
  cursor: pointer;
  font: inherit;
  font-size: 1.3em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.Button .arrowIconRight, .Button .arrowIconLeft {
  font-size: 1.2em !important;
  color: azure;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.buttonP {
  margin: 10px 6px;
}

.Button:hover {
  background-color: rgba(33, 33, 33, 0.7);
  color: #ff5722;
  border: 3px solid azure;
}

.Button:hover .arrowIconRight {
  color: #ff5722;
  -webkit-transform: rotate(-180deg);
      -ms-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

.Button:hover .arrowIconLeft {
  color: #ff5722;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

@media (max-width: 500px) {
  .Button {
    font-size: 1.2em;
  }

  .Button .arrowIconRight, .Button .arrowIconLeft {
    font-size: 1.1em !important;
  }
}


