.NavItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 100%;
}

.logoRoot {
  width: 20%;
  height: 80%;
  margin: 0px 25px;
}

.leftLinks, .rightLinks {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 40%;
}

.onlyOnMobile {
  display: none;
}

@media (max-width: 500px) {
  .onlyOnMobile {
    display: block;
  }
  
  .NavItems {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
  }

  .leftLinks, .rightLinks {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
  }

  .logoRoot {
    display: none;
  }
}


